@use 'variables' as v;

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}
@mixin xl_desktop {
    @media (min-width: v.$xl_desktop) {
        @content;
    }
}

@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1rem 4rem;
    color: $colorTexto;
    font-size: 1.8rem;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    display: inline-block;

    &:hover {
        background-color: darken( $bgColor, 10% );
        cursor: pointer;
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin contenedor {
    width: min(95%, 120rem);
    margin: 0 auto;
}

@mixin contenedor-sm {
    width: min(95%, 60rem);
    margin: 0 auto;
}

@mixin headings {
    font-family: v.$fuente_principal;
    font-weight: v.$black;
    margin: 2rem 0;
}

@mixin nombre-pagina {
    @include headings;
    margin: 6rem 0 3rem 0;
    text-align: center;
}

@mixin texto-fluido($min, $max) {
    font-size: clamp($min, -0.8rem + 10vw, $max);
}

@mixin texto-degradado($incio, $fin) {
    background: linear-gradient(to right, $incio, $fin);
    background-clip: text;
    color: transparent;
}

@mixin menu-social($color, $size) {

    &__enlace {
        color: $color;
        transition: all .3s ease-in-out;
        &::before {
            font-size: $size;
            display: inline-block;
            font-style: normal;
            text-rendering: auto;
            -webkit-font-smoothing: antialiased;
            font-family: "Font Awesome 6 Brands";

        }

        &[href*="https://www.facebook.com"]::before {
            content: "\f39e";
        }
        &[href*="https://www.instagram.com"]::before {
            content: "\f16d";
        }
        &[href*="https://github.com"]::before {
            content: "\f09b";
        }
        &[href*="https://www.linkedin.com/in"]::before {
            content: "\f08c";
        }
        &[href*="https://www.youtube.com"]::before {
            content: "\f167";
        }

        &:hover {
            color: rgb(158, 158, 158);
            transform: scale(.95);
        }
    }
}